.submenu {
  width: 100%;
  height: 90px;
  background: {
    color: $color-dark; }

  .infos {
    padding: 25px 20px;
    width: 50%;
    float: left;
    color: $color-soft;
    text-transform: uppercase;
    opacity: .8;

    @media #{$small-max} {
      width: calc(100% - 130px); }

    .title {
      font: {
        size: 1.6em; } }

    .subtitle {
      font: {
        size: 1.2em; }
      opacity: .5;
      margin: {
        left: 10px; } } }

  .options {
    width: 50%;
    float: right;
    opacity: .4;

    @media #{$small-max} {
      width: 130px; }

    .items {
      .item {
        .btn {
          position: relative;
          float: right;
          margin: 25px 0;
          padding: 10px 10px 10px 40px;
          font: {
            weight: 700; }

          @media #{$small-max} {
            padding: 0;
            margin: 30px 10px;
            min-width: 32px;
            height: 32px; }

          &:hover {
            .icon {
              background-image: url('../images/icons-gold.png');
              opacity: .8; } }

          .icon {
            @include transition(all .4s);
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            margin: auto; }

          .text {
            @media #{$small-max} {
              display: none; } } } } } } }
